<template>
	<view class="content">
		<narBar typeName="我的" type2Name="已购" :type="narBar" @serach="search"  @gotoNarBar="gotoNarBar()"></narBar>
		<view class="warp">
			
			<view class="sell">
				<view class="left">
					<view @click="gotoSellDetails(item.id)" v-for="(item,index) in leftList" class="item" :key="item.id">
						<sell-card  :sellItem="item"></sell-card>
					</view>
				
		
				</view>
				<view class="right">
					<view  @click="gotoSellDetails(item.id)" v-for="(item,index) in rightList" class="item" :key="item.id">
						<sell-card :sellItem="item"></sell-card>
					</view>
				</view>
			</view>
			
			<myLoad :status="status"></myLoad>
			
			
		</view>
		
		
		<!-- 悬浮窗口 -->
		<uni-fab ref="fab"  :pattern="pattern" :content="content" :horizontal="horizontal" :vertical="vertical"
				:direction="direction" @trigger="trigger" />
		
		
		
		
	</view>
	
</template>

<script>
	import {getSellList} from "@/api/sell.js"
	import sellCard from '@/components/sellCard/sellCard.vue'
	export default {
		components:{
			sellCard
		},
		data() {
			return {
			  narBar:1,
			  status: 'loading',
			  leftList: [], //左边列图片
			  rightList: [], //右边列图片
			  leftHeight: 0, //左边列高度
			  rightHeight: 0, //右边列高度
			  columnWidth: 370, //列宽度
			  sellList:[],
			  // 查询参数s
			  queryParams: {
			  	pageNum: 1,
			  	pageSize: 5,
			  	orderType: 1,
			  	name:""
			  },
			  //总条数
			  total:0,
			  //悬浮窗：
			  pattern: {
			  	color: '#7A7E83',
			  	backgroundColor: '#fff',
			  	selectedColor: '#fac843',
			  	buttonColor: '#fac843',
			  	iconColor: '#fff'
			  },
			  
			  horizontal: 'right',
			  vertical: 'bottom',
			  direction: 'vertical',
			  content: [{
			  		iconPath: 'iconfont icon-fabiao icon-active',
			  		// selectedIconPath: '/static/image-active.png',
			  		text: '发布',
			  		active: false
			  	},
			  	{
			  		iconPath: 'iconfont icon-icon- icon-active',
			  		// selectedIconPath: '/static/home-active.png',
			  		text: '顶部',
			  		active: false
			  	},
			  	{
			  		iconPath: 'iconfont icon-shuaxin icon-active',
			  		// selectedIconPath: '/static/star-active.png',
			  		text: '刷新',
			  		active: false
			  	}
			  ]
			}
		},
	
		//触底
		onReachBottom(){
			// console.log("触底")
			this.handleCurrentChange();
		},
		//下拉刷新
		onPullDownRefresh() {
			this.queryParams.pageNum = 1;
			this.leftList = [];
			this.rightList = [];
			this.leftHeight=0;
			this.rightHeight=0;
			this.getSellList().then(()=>{uni.stopPullDownRefresh()})
		},
		mounted() {
			// this.$nextTick(() => {
			// 	uni.createSelectorQuery().in(this).select('.left').boundingClientRect(res => {
			// 		this.columnWidth = res.width
			// 		//方法1
				
					
			
			// 	}).exec()
			// })
		},
		onLoad() {
			this.getSellList();
		},
		methods: {
			search(val){
				this.queryParams.name = val
				this.queryParams.pageNum = 1;
				this.leftList = [];
				this.rightList = [];
				this.leftHeight=0;
				this.rightHeight=0;
				this.getSellList();
			},
			pullTop() {
				uni.pageScrollTo({
					scrollTop: 0,//滚动到距离顶部为0
					duration: 500//滚动时长
				})
			},
			
			/**
			 * 悬浮按钮项点击事件
			 * @param {Object} e
			 */
			trigger(e) {
				// console.log(e)
				this.$refs.fab.close();
				if(e.index==0){
					uni.navigateTo({
						url:"/pages/addSell/addSell"
					})
				} else if(e.index==1){
					this.pullTop();
				} else{
					// this.pullTop();
					uni.startPullDownRefresh();
				}
				// this.content[e.index].active = !e.item.active
			
				
			},
			
			//分页
			handleCurrentChange(){
				if(!this.isMore()){
					this.status = "nomore"
					return ;
				}
				// Vue.set(this.queryParams,pageNum,page.pageNum)
				this.queryParams.pageNum++;
				this.queryParams.pageSize;
				this.getSellList();
			},
			/**
			 * sell列表
			 */
			async getSellList(){
				
				this.status = "loading"
				const res = await getSellList(this.queryParams)
				this.total =  res.data.total
				if(!this.isMore())
					this.status = "nomore"
				else
					this.status = "more"
				
				await this.setWaterFallLayout(res.data.rows)
				
				
					
			},
			isMore(){
				if(this.queryParams.pageNum*this.queryParams.pageSize >= this.total){
					// console.log("无数据")
					return false;
				}
				return true;
			},
			
			gotoNarBar(val){
				// console.log(val)
				this.narBar = val;
			},
			gotoSellDetails(id){
				// console.log(id)
				uni.navigateTo({
					url:`/pages/sellDetail/sellDetail?sellId=${id}`
				})
			
			},
		 
			
			async setWaterFallLayout(sellList) {
				for (let item of sellList) {
				
					let pictures = item.sellPictures
					if(pictures!=null&&pictures.length>0){
						try {
							let picture = pictures[0];
							let h = 10
							if(picture.height!=null&&picture.width!=null){
								//费流量（哭）
								h +=  picture.height / picture.width * (this.columnWidth/2)
							}else{
								h += await this.getImgHeight(this.getUrl(picture.url))
							}
							
							// console.log(h)
							if (this.leftHeight <= this.rightHeight) {
								this.leftList.push(item)
								this.leftHeight += (h+90)
							} else {
								this.rightList.push(item)
								this.rightHeight += (h+90)
							}
						} catch (e) {
							// console.log(e)
						}
					}									
					else{
						if (this.leftHeight <= this.rightHeight) {
							this.leftList.push(item)		
							this.leftHeight+=90;
						} else {
							this.rightList.push(item)
							this.rightHeight+=90;
							
						}
					}	

				}

			},
			//api获取图片高度
			getImgHeight(path) {
				return new Promise((resolve, reject) => {
					
					uni.getImageInfo({
							src: path,
							success:  (image)=> {
								const h = image.height / image.width * (this.columnWidth/2)
								resolve(h)
							},
							fail() {
								reject('error')
							}
						});
				
				})
			},
			
		},
		watch:{
			narBar(newval,oldval){
				this.queryParams.pageNum = 1;
				this.queryParams.orderType = newval
				this.leftList = [];
				this.rightList = [];
				this.leftHeight=0;
				this.rightHeight=0;
				this.getSellList().then(()=>{uni.stopPullDownRefresh()})
			}
		}
		
	}
</script>

<style>
.sell{
	width: calc(100% - 10px);
	display: flex;
	align-items: flex-start;
	margin: 0 5px;
}
.sell .item{
	margin: 10px 5px;
	
}
.sell > .left,.sell > .right{
	width: 370rpx; 
}
.content{
	padding-top: 55px;
}
.warp{
	margin: 0px;
}

</style>
